/* ------------------------------------------------------------ *\
	Animations
\* ------------------------------------------------------------ */
@keyframes bottomToTop {
  0% {
    top: auto;
    bottom: 0;
    left: 0;
  }
  33% {
    bottom: 0;
    left: -100%;
  }
  66% {
    bottom: auto;
    top: 0;
    left: -100%;
  }
  100% {
    bottom: auto;
    top: 0;
    left: 0;
  }
}
@keyframes topToBottom {
  0% {
    bottom: auto;
    top: 0;
    left: 0;
  }
  33% {
    top: 0;
    left: 100%;
  }
  66% {
    top: auto;
    bottom: 0;
    left: 100%;
  }
  100% {
    top: auto;
    bottom: 0;
    left: 0;
  }
}
