/* ------------------------------------------------------------ *\
	Game-holder
\* ------------------------------------------------------------ */
.game-holder {
  height: 100vh;
  max-width: 100vw;
  padding-block: 20vh;
  overflow: hidden;
}

.gameboard {
  height: 60vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 20rem;
  border-top: 0.2rem solid #e6e6e6;
  border-bottom: 0.2rem solid #e6e6e6;
}

.gamemap {
  position: relative;
}
.gamemap .gamemap__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.gamemap .gamemap__image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.tile {
  position: relative;
  height: 14vh;
  width: 14vh;
  border: 0.1rem solid #e6e6e6;
  transition: 0.5s background-color;
}
.tile:hover {
  background-color: rgba(230, 230, 230, 0.2);
}

.chibi-holder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 1.5s transform;
  z-index: 2;
}
.chibi-holder img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
}

.chibi-holder--rotated img {
  transform: translate(-50%, -50%) rotateY(180deg);
}

.body {
  z-index: 1;
}

.hair {
  z-index: 3;
}

.dress {
  z-index: 2;
}

.weapon {
  z-index: 0;
}

#brann .weapon {
  z-index: 4;
}

/* ------------------------------------------------------------ *\
	Grid
\* ------------------------------------------------------------ */
.grid {
  display: flex;
  flex-flow: row wrap;
  /*  Grid col 1of2  */
  /*  Grid col 1of3  */
}
.grid .grid__col {
  max-width: 100%;
  flex: 1;
}
.grid .grid__col--1of2 {
  max-width: 50%;
  flex: 0 0 50%;
}
.grid .grid__col--1of3 {
  max-width: calc(100% / 3);
  flex: 0 0 calc(100% / 3);
}

