/* ------------------------------------------------------------ *\
	Player
\* ------------------------------------------------------------ */
.player {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  width: 100%;
  height: 20vh;
  background-position: center center;
}
.player .player__info {
  height: 100%;
  display: grid;
  grid-template-columns: 0.8fr 1fr 0.5fr;
  justify-content: center;
  align-items: center;
  background-color: rgba(18, 18, 18, 0.5);
  backdrop-filter: blur(1rem);
  border-radius: 0 2rem 2rem 0;
  border: 0.1rem solid #e6e6e6;
  text-align: center;
  z-index: 1;
}
.player .player__image {
  display: flex;
  justify-content: center;
  align-items: center;
}
.player .player__image img {
  width: 15vh;
  height: 15vh;
  display: block;
  border: 0.2rem solid #e6e6e6;
}
.player .player__skills {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1vh;
}
.player .player__skills li {
  width: 5vh;
  height: 5vh;
  border: 0.1rem solid #e6e6e6;
}
.player .player__skills li:hover img:nth-child(2) {
  opacity: 1;
}
.player .player__skills li img:nth-child(1) {
  width: 100%;
  height: 100%;
  display: block;
}
.player .player__skills li img:nth-child(2) {
  max-width: 20rem;
  width: 20rem;
  height: 30rem;
  position: absolute;
  left: 6vh;
  opacity: 0;
  top: 0;
  transition: 0.5s opacity;
  pointer-events: none;
  z-index: 2;
}
.player .player__actions {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.player .player__actions li {
  position: relative;
}
.player .player__actions .action {
  border: 0.1rem solid #e6e6e6;
  width: 10vh;
  height: 15vh;
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgba(230, 230, 230, 0.2);
  backdrop-filter: blur(1rem);
}
.player .player__actions .normal {
  transform: translate(-50%, -50%);
}
.player .player__actions .extra {
  transform: translate(-25%, -35%);
}

.player1 {
  position: absolute;
  bottom: 0;
  left: 0;
}

.player2 {
  position: absolute;
  top: 0;
  left: 0;
}
.player2 .player__skills li img:nth-child(2) {
  top: auto;
  bottom: 0;
}

#player-up {
  z-index: 20;
}
#player-up li img:nth-child(2) {
  top: 0;
  bottom: auto;
}

#player-down {
  z-index: 20;
}
#player-down li img:nth-child(2) {
  top: auto;
  bottom: 0;
}

